}

.menu-icon{
    font-size: 2rem;
    text-align: center;
    display: none;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 1px;
    list-style: none;
    text-align: end;
    width: 50%;
    justify-content: end;
}

.nav-link{
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
    border-radius: 10px;
}

.nav-link:hover{
    background-color: #11339955;
    border-radius: 20px;
    color: rgb(125, 225,255);
    transition: all 0.2s ease-out;
    font-size: 37px;
}


@media screen and (max-width: 1040px) {
    .NavItems{
        position: relative;
    }
    .nav-menu{
        display: flex;
        flex-direction: column;
        width: 80%;
        height: 90px;
        position: absolute;
        top: -40px;
        left: 0;
        opacity: 0;
        transition: all 0.5s ease;
        
    }

    .nav-menu.active{
        background: black;
        left: 0;
        width: 80%;
        height: 245px;
        opacity: 0.9;
        transition: all 0.5 ease;
        z-index: 1;
    }

    .nav-link{
        text-align: center;
        padding: 0.01rem;
        width:100%;
        display: table;
        margin-right: 5rem;
    }

    .nav-link:hover {
        background-color: #222266;
        border-radius: 0;
    }

    .menu-icon{
        display:flex;
        min-width: 40px;
        min-height: 80px;
        position: absolute;
        top: 0;
        right: 0;
        font-size: 2rem;
        cursor: pointer;
        background-color: black;
        color: gray
    }
    
}


