.wallet{
    border-radius: 7px;
    border-style: solid;
    border-width: 2px;
    max-width: 30%;
    min-width:fit-content;
    height: fit-content;
    margin-left: 87%;
    font-size: 5px;
    text-align: center;
    padding: 0px 2px;
    margin-top: 5px;
    margin-right: 3px;
    font-weight: bolder;
    position:absolute;
}

.wallet-button{
    background-color: #00000000;
    border:hidden;
    font-size: 13px;
    font-family:serif;
    
}

.connected{
    color: rgb(2, 175, 2);
    background-image: linear-gradient(rgb(3, 3, 184), rgb(3, 3, 70));
    
}
.not-connected{
    color: gray;
    background-image: linear-gradient(rgb(125, 125, 138), rgb(26, 26, 27));
}

.text{
    padding: 0px;
}